body {
  margin: 30px 40px;
  color: #696969;
  font-family: sans-serif;
  font-size: 48px;
  font-weight: lighter;
  line-height: 1.6;
}

.fillin {
  font-weight: normal;
}

.lines {	
  overflow-wrap: break-word;
}

.line-input {
  border: 0;
  font-family: inherit;
  font-size: inherit;
  color: inherit;
  font-weight: inherit;
}

.line-input:focus {
  outline: none !important;
}

.button {
  display: block;
  margin-top: 23px;
  width: 200px;
  font-size: 32px;
  padding: 20px 40px;
  border-radius: 47px;
  background-color: #ececec;
}

.button:hover {
  background-color: #fffdd0;
  cursor: pointer;
}

.buttonClicked {
  animation: bgcolor 1s 1;
}

@keyframes bgcolor {
  from {background-color: #ffcc00;}
  to {background-color: #ececec;}
}

.footer {
  margin-top: 2041px;
  font-size: 16px;
}

@media (max-width: 480px) {
  body {
    font-size: 24px;
  }

  .button {
    margin-top: 18px;
    width: 150px;
    font-size: 20px;
    padding: 12px 24px;
    border-radius: 31px;    
  }

  .button:hover {
    background-color: #ececec;
  }

  .footer {
    font-size: 17px;
  }
}
